<template>
  <v-layout>
    <v-layout id="DesktopNav">
      <DesktopNav></DesktopNav>
    </v-layout>

    <v-layout id="MobileNav">
      <MobileNav></MobileNav>
    </v-layout>
  </v-layout>
</template>

<script>
import DesktopNav from './DesktopNav'
import MobileNav from './MobileNav'

export default {
  name: 'Nav',
  components: {
    MobileNav,
    DesktopNav
  }
}
</script>

<style scoped>
#DesktopNav {
  position: fixed;
  width: 100vw;
  height: 64px;
}
#MobileNav {
  position: fixed;
  width: 100vw;
  height: 64px;
}
@media (max-width: 800px) {
  #DesktopNav{
    display: none;
  }
}
  @media (min-width: 800px) {
    #MobileNav {
      display: none;
    }
  }
</style>



